<style scoped>
    .item {
        margin-bottom: 20px;
        border: 1px solid #d4d4d4;

    }

    .item-div {
        width: 100%;
        height: 250px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        text-align: right;
        padding: 10px;
    }

</style>
<template>
    <div style="padding: 20px">
        <Row>
            <Col :span="12">
                <h2>场馆预定</h2>
            </Col>
            <Col :span="12" style="text-align: right">
                <a class="link-a">
                    <Button size="large" type="info" @click="my"> 我的预约</Button>
                </a>
            </Col>
        </Row>

        <Row style="padding: 20px">
            <Col style="padding-right: 20px" :xs="12" :sm="8" :md="6" v-for="(item,index) in listData.data"
                 v-bind:key="index">
                <router-link :to="{name:'reservationDetail',params:{id:item.id}}">
                    <div class="item">
                        <div class="item-div" :style="{'backgroundImage':'url('+$url+item.picture+')'}">
                        </div>
                        <div style="padding: 10px;">
                            <h3>{{item.name}}</h3>
                            <div style="line-height: 30px">{{ item.remark }}</div>
                            <div style="text-align: right">
                                <Button size="large" type="info">预约</Button>
                            </div>
                        </div>
                    </div>
                </router-link>
            </Col>
        </Row>
        <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
            <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
                  :current="listData.current_page" size="small"/>
        </Row>
    </div>
</template>

<script>
  import api from './api';

  export default {
    data() {
      return {
        listData: {},
        formData: {},
      };
    },
    mounted() {
      this.loadData();
    },
    methods: {
      loadData(page) {
        if (page) {
          this.formData.jump_page = page;
        }
        api.index(this.formData, res => {
          this.listData = res.data.data;
        });
      },
      my(){
        this.$router.push({name:'reservationMy'})
      }
    }
  };
</script>
